import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "1b0fde3a-3c72-4f1a-8a92-66508a392dc3"
    }
})

export const API = {
    getUsers(currentPage = 1, pageSize = 10) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(responcse => {
                return responcse.data
            })
    },

    AuthMe() {
        return instance.get(`auth/me`).then(responcse => {
            return responcse.data
        })
    },

    getProfile(ProfileID = 2) {
        return instance.get(`profile/` + ProfileID)
            .then(responcse => {
                return responcse.data
            })
    },
    DelSub(userId) {
        return instance.delete(`follow/${userId}`).then(response => {
            return response.data
        })
    },

    AddSub(userId) {
        return instance.post(`follow/${userId}`).then(response => {
            return response.data
        })
    },
    getProfileStatus(ProfileID = 2) {
        return instance.get(`profile/status/` + ProfileID)
            .then(responcse => {
                return responcse.data
            })
    },
    setProfileStatus(status) {
        return instance.put(`profile/status/`,{status:status})
            .then(responcse => {
                return responcse.data
            })
    },

Login(email,password,rememberMe) {
    return instance.post(`auth/login/`,{email : email,
    password : password, rememberMe : rememberMe})
        .then(responcse => {
            return responcse.data
        })
},
    LogOut() {
        return instance.delete(`auth/login`)
            .then(responcse => {
                return responcse.data
            })
    }

}
